<template>
	<view class="content">
		
		<view class="member-bg">
			<image src="../../static/image/member-bg.png" mode="widthFix" lazy-load></image>
		</view>
		<view class="member-top">
			<view class="member-top-info">
				<view class="">{{current.length>0?`${current[0].title}${i18n.membership_card}`:i18n.No_membership_card}}</view>
			</view>
			<view class="member-top-message">
				<view class="items">
					<text>{{i18n.Current_total_revenue}}(USDT)</text>
					<view>{{synum?synum:'0'}}</view>
				</view>
				<view class="items">
					{{current.length>0?`${i18n.You_are_currently} ${current[0].title}${i18n.membership_card}`:i18n.No_membership_card}}
				</view>
			</view>
		</view>
		
		
		<view class="member-grade">
			<view class="title">
				{{i18n.Upgrade_membership_card}} <text>{{i18n.Boost_for_more_revenue}}</text>
			</view>
			<view class="member-list">
				<block v-for="(item,index) in list" :key="index">
					<view class="member-list-item grap1" :class="[`grap${item.lv_m}`]">
						
						<image :src="`../../static/image/member0${item.lv_m}.png`" mode="widthFix" lazy-load></image>
						
						<view class="item-l">
							<text>{{item.title}}{{i18n.membership_card}}</text>
							<view class="item-l-text">
								{{item.freeze}} <text>USDT</text>
							</view>
						</view>
						
						<view class="item-r" hover-class="active" @tap.stop="closePopule(item)">
							{{i18n.Promote_now}}
						</view>
					</view>
				</block>
			</view>
		</view>
	<view class="popule" v-if="showPopule" @tap.stop="closePopule('exit')">
		<view class="popule-content" @tap.stop="''">
			<view class="popule-list">
				<view>{{i18n.upgrade}}{{populeInfo.title}}{{i18n.membership_card}}</view>
				<text>{{i18n.value}}：{{populeInfo.freeze}} UDST</text>
			</view>
			<view class="popule-list popule-money">
				<text>{{i18n.Actual_price_paid}}</text>
				<view>{{populeInfo.freeze}} UDST</view>
			</view>
			<view class="popule-list grap-bg">
				<text>{{i18n.Consumption_of_computing_power}}</text>
				<view>{{populeInfo.sl}} USDT</view>
			</view>
			<view class="popule-list">
				<text>{{i18n.yanzhengma}}</text>
				<view>
					<input type="text" v-model="yzm" :placeholder="i18n.enter_vcode" maxlength="6"> 
					<vcode :account="phone"></vcode></view>
			</view>
			<view class="popule-tip">
				<view>{{i18n.description}}</view>
				<text>{{populeInfo.ps}}</text>
			</view>
			<view class="popule-tip">
				<view>{{i18n.Account_Balance}}</view>
				<text>{{usdt_balance}} USDT</text>
			</view>
			<view class="popule-nav">
				<view>
					<button type="default" hover-class="active" @tap.stop="closePopule('exit')">{{i18n.cancel}}</button>
				</view>
				<view>
					<button type="default" hover-class="active" @tap.stop="comfirm">{{i18n.Place_an_order}}</button>
				</view>
			</view>
		</view>
	</view>
	<load v-if="laoding"></load>
	<pay-pwd
	:maxlength="6" 
	:isPwd="true" 
	:isSHow="shows" 
	:isShows="isshows"
	:isShowss="showss"
	@close="close"
	@input-again="inputAgain"
	@finish="finish"></pay-pwd>
	</view>
</template>

<script>
	import Load from '@/components/common/load.vue';
	import payPwd from '@/components/pay-pwd/pay-pwd.vue';
	import {pageto, showToast, fetch, forceUpdate} from '@/common/js/util.js';
	import {mapGetters,mapMutations} from "vuex";
	import vcode from '@/components/vcode/vcode.vue';
	export default {
		components:{
			payPwd,Load,vcode
		},
		data() {
			return {
				laoding:false,
				showPopule:false,
				shows:false,
				showss:false,
				isshows:false,
				list:[],
				current:[],
				synum:'',
				populeInfo:{},
				usdt_balance:'',
				lv_m:'',
				phone:'',
				yzm:'',
				populeInfos:{},
				isCheck:false
			};
		},
		computed:{
			i18n() {
				return this.$t('message')
			},
			...mapGetters([
				'getRequestUrl',
				'getLangType',
				'getLogin',
				'getLoginInfo'
			]),
		},
		onLoad(){
			this.phone = this.getLoginInfo.email
			this.getMember();
			if(this.getLangType=='en'){
				uni.setNavigationBarTitle({
					title:'Membership Card'
				})
			}
		},
		onShow(){
			forceUpdate(this.getLangType);
		},
		methods: {
			getMember(){
					let data = {
					id:this.getLoginInfo.id,
					token:this.getLoginInfo.token
					}
					if(this.isCheck) return
					this.isCheck  = true;
					fetch('/api/Shop/shop_list',data,'post')
					.then(res=>{
						this.isCheck  = false;
						if(res.data.code){
							this.synum = res.data.data.synum
							this.list = res.data.data.vip_list;
							this.usdt_balance = res.data.data.usdt_balance;
							this.lv_m =  res.data.data.lv_m;
							this.list.forEach((item,index)=>{
								item.freeze = parseFloat(item.freeze);
								// if(item.lv_m==3){
								// 	this.lv_m=item.lv_m
								// }
							})
							this.current = this.list.filter((item,index)=>{
								return item.lv_m==this.lv_m;
							})
						}
					})
					.catch(err=>{
						this.isCheck  = false;
						console.log(err);
					})
			},
			comfirm(){
				// console.log(this.populeInfos);
				if(!this.yzm) {showToast(this.i18n.enter_vcode);return false;};
				this.closePopule();
				this.shows = true;
			},
			closePopule(item) {
				if(item=='exit'){
					this.showPopule = !this.showPopule;
					this.yzm='';
					return;
				}else if(!item){
					this.showPopule = !this.showPopule;
				}else{
					this.yzm='';
					this.populeInfos = item;
					if(item){
						if(!this.current.length){
							if(item.lv_m==1 || item.lv_m==2){
								this.populeInfo = item;
								this.showPopule = !this.showPopule;
							}
						}else{
							if(this.lv_m==2){
								this.populeInfo = item;
								if(item.lv_m==1 || item.lv_m==2){
									if(item.lv_m==1){
										showToast(this.i18n.member_tip0)
									}else if(item.lv_m==2){ 
										showToast(this.i18n.member_tip1)
									}
									return false;
								}
								this.showPopule = !this.showPopule;
								if(item.lv_m==3){
									this.populeInfo.sl =  parseFloat(item.sl)  - parseFloat(this.current[0].sl)
								}
							}else if(this.lv_m==1){
								this.populeInfo = item;
								this.populeInfo.sl = parseFloat(item.sl)
								if( item.lv_m==1 ||item.lv_m==3 ){
									if(item.lv_m==1){
										showToast(this.i18n.member_tip1)
									}
									if(item.lv_m==3){
										showToast(this.i18n.member_tip2)
									}
									return false;
								}
								this.showPopule = !this.showPopule;
							}else{
								this.populeInfo = item;
								if(item.lv_m==1 || item.lv_m==2){showToast(this.i18n.member_tip0);return false;}
								this.showPopule = !this.showPopule;
								this.populeInfo.sl = parseFloat(item.sl)
							}
						}
					}
				}
				
			},
			close(val){
				if(val==1){
					this.isshows = false;
					this.showss = false;
					this.shows = false;
				}else{
					this.isshows = val;
					this.shows = val;
					this.showss = val
				}
			},
			inputAgain(val){
				this.isshows = false;
				this.shows = true;
			},
			finish(val){
				console.log(this.yzm);
				let data = {
					id:this.getLoginInfo.id,
					token:this.getLoginInfo.token,
					lang_type:this.getLangType=='chs'?1:2,
					paypwd:val,
					shopid:this.populeInfos.id,
					token_id:3,
					type:1,
					yzm:this.yzm
				}
				if(this.isCheck) return
				this.isCheck  = true;
				this.laoding = true;
				fetch('/api/shop/buy_shop',data,'post').then(res=>{
					this.laoding = false;
					console.log(res);
					this.isCheck  = false;
					showToast(res.data.msg)
					if(res.data.code){
						this.isshows = false;
						this.showss = false;
						this.shows = false;
						this.yzm='';
						this.getMember();
					}else{
						this.isshows = false;
						this.showss = false;
						this.shows = false;
						if(res.data.msg=='支付密码错误！'){
							this.isshows = true;
						}else if(res.data.msg=='usdt不足'){
							this.isshows = false;
							this.shows = false;
							this.showss = true;
						}else{
							this.yzm='';
						}
					}
				}).catch(error=>{
					this.isCheck  = false;
					this.laoding = false;
				})
			}
		},
	}
</script>
<style>
	page{
		background-color: #fffeff;
	}
</style>
